import React, {useState} from 'react'
import Ingles from '../../img/ingles.png'
import Espana from '../../img/espana.png'

const NavbarLinks = (props) => {
    const [espaol, setEspaol] = useState(true)

    const handleLenguage = () => {
        if(espaol) {
            setEspaol(false)
        } else {
            setEspaol(true)
        }
    }

    return (
        <nav className={props.active ? "navbar-links is-active" : "navbar-links"}>
            <a href="#about">About</a>
            <a href="#projects">Projects</a>
            <a href="#contact">Contact</a>
            {espaol 
                ? <img onClick={handleLenguage} src={Espana} alt='Espaa'></img>
                : <img onClick={handleLenguage} src={Ingles} alt='English'></img>}          
        </nav>
    )
}

export default NavbarLinks
