import React, {useState} from 'react'
import { useForm } from 'react-hook-form'

const FormContact = () => {
    const [isSubmit, setIsSubmit] = useState(undefined)
    const {register, formState: { errors }, handleSubmit} = useForm();


    const onSubmit = (data) => {
        console.log(data)

        setIsSubmit(true)

        setTimeout(() => {
            setIsSubmit(undefined)
        }, 3000);
    }

    return (
        <>
            
            <form className='form' onSubmit={handleSubmit(onSubmit)} id='form-contact' action="https://formsubmit.co/palaciosleandroad@gmail.com" method="POST">
                <h2 className='form-title'>Enviame un mensaje!</h2>
                <input 
                    type="text" 
                    name="name" 
                    placeholder='Nombre *' 
                    {...register("name",  { required: true, pattern: /[a-zA-Z]/ })} 
                />
                {errors.name?.type === 'required' && <p className='error-pattern'>El nombre es requerido</p>}
                {errors.name?.type === 'pattern' && <p className='error-pattern'>Ingrese un nombre vlido</p>}
                <input 
                    type="email" 
                    name="email" 
                    placeholder='Email *' 
                    {...register("email", { required: true, pattern: /[a-z0-9]+@[a-z]+\.[a-z]{2,3}/})} 
                /> 
                {errors.email?.type === 'pattern' && <p className='error-pattern'>Ingrese un correo vlido</p>}
                {errors.email?.type === 'required' && <p className='error-pattern'>El correo es requerido</p>}
                <textarea 
                    name="message" 
                    placeholder='Mensaje *' 
                    {...register("message", { required: true, maxLength: 300 })} 
                />
                {errors.message?.type === 'required' && <p className='error-pattern'>El mensaje es requerido</p>}
                {errors.message?.type === 'maxLength' && <p className='error-pattern'>El mensaje no puede superar los 300 caracteres</p>}
                <input type="submit" value="SUBMIT" />

            </form>
            {isSubmit
                ? <p className="enviado">Mensaje enviado correctamente!</p>
                : isSubmit === undefined 
                    ? <p></p> 
                    : <p className="error">No se pudo enviar!</p>}
        </>
    )
}

export default FormContact


